import mSearch from "@/components/mehaotian-search/mehaotian-search.vue";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      val: "",
      placeholder: "搜索领券"
    };
  },
  components: {
    mSearch
  },
  computed: {
    ...mapState(["searchHistory"])
  },
  methods: {
    ...mapMutations(["clearHistory"]),
    clear() {
      let _this = this;
      uni.showModal({
        title: "提示",
        content: "确定要清空所有搜索历史吗？",
        success: function(res) {
          if (res.confirm) {
            _this.clearHistory();
          } else if (res.cancel) {
            // console.log('用户点击取消');
          }
        }
      });
    },
    search(item) {
      uni.navigateTo({
        url: `../product/goods-list?key=${item}`
      });
    }
  }
};
